<template>
  <div>
    <!-- -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="menua">
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true">
          <el-form-item>
            <el-input v-model="medicname" prefix-icon="el-icon-search" clearable placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="selectmedic" type="primary"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="resetselect" type="primary"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="addmedicdialog = true" type="primary"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table :data="medicinedata" v-loading="listLoading" style="width: 100%" border>
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="id" width="100" label="ID" sortable></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="price" label="()"></el-table-column>
        <el-table-column prop="date" label=""></el-table-column>
        <el-table-column prop="type" label=""></el-table-column>
        <el-table-column prop="num" label="()"></el-table-column>
        <el-table-column prop="note" label=""></el-table-column>
        <el-table-column width="200" label="">
          <template scope="scope">
            <el-button size="small" @click="editmedichandle(scope.row)"></el-button>
            <el-button type="danger" @click="deltemedicine(scope.row)" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- -->
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="total"
        :page-size="pagesize"
        :page-count="pagecount"
        :current-page="currentpage"
        @current-change="currentpagehandle"
        style="margin-top:10px"
      ></el-pagination>
      <!-- dialog -->
      <el-dialog title="" :visible.sync="editmedicdialog">
        <div class="editposit">
          <el-form ref="editform" :model="editform" label-width="70px" size="mini">
            <el-form-item label="ID">
              <el-input v-model="editform.id" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editform.name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input-number v-model="editform.price" :precision="2" :step="1" :min="0"></el-input-number>
            </el-form-item>
            <el-form-item label="">
              <el-date-picker
                :picker-options="pickerOptions"
                v-model="editform.date"
                type="date"
                format="yyyy  MM  dd "
                value-format="yyyy-MM-dd"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editform.type"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input-number v-model="editform.num" :step="1" :min="0"></el-input-number>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" v-model="editform.note"></el-input>
            </el-form-item>

            <el-button @click="editmedicdialog = false" size="mini"></el-button>
            <el-button @click="editmedic" type="primary" size="mini"></el-button>
          </el-form>
        </div>
      </el-dialog>
      <!-- dialog -->
      <el-dialog title="" :visible.sync="addmedicdialog">
        <el-form
          style="width:300px"
          ref="editform"
          :model="editform"
          label-width="70px"
          size="mini"
        >
          <el-form-item label="">
            <el-input v-model="addform.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input-number v-model="addform.price" :precision="2" :step="1" :min="0"></el-input-number>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              :picker-options="pickerOptions"
              v-model="addform.date"
              type="date"
              format="yyyy  MM  dd "
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addform.type"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input-number v-model="addform.num" :step="1" :min="0"></el-input-number>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="addform.note"></el-input>
          </el-form-item>
          <div>
            <el-button @click="addmedicdialog = false" size="mini"></el-button>
            <el-button @click="addmedic" type="primary" size="mini"></el-button>
          </div>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  created() {
    this.getallmedicine();
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      listLoading: false,
      medicname: "",
      editmedicdialog: false, //dialog
      addmedicdialog: false, //dialog
      medicinedata: [], //
      total: 0, //
      pagesize: 6, //
      pagecount: 0, //
      currentpage: 1, //
      editform: {
        id: 0,
        image: "",
        name: "",
        price: 0,
        date: "",
        type: "",
        num: 0,
        note: ""
      },
      addform: {
        image: "",
        name: "",
        price: 0,
        date: "",
        type: "",
        num: 0,
        note: ""
      }
    };
  },
  methods: {
    // 
    getallmedicine() {
      this.$getRequest(
        "/getallmedicine?" +
          "pageNo=" +
          this.currentpage +
          "&pageSize=" +
          this.pagesize +
          "&parms=" +
          this.medicname
      ).then(res => {
        if (res.data.code == 200) {
          this.medicinedata = res.data.data.list;
          this.total = res.data.data.total; //
          this.pagesize = res.data.data.pageSize; //
          this.pagecount = res.data.data.pages; //
          this.currentpage = res.data.data.pageNum; //
        }
      });
    },
    //
    currentpagehandle(val) {
      this.currentpage = val;
      this.getallmedicine();
    },
    //
    deltemedicine(row) {
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          this.$deleteRequest("/deltemedicine?id=" + row.id)
            .then(res => {
              this.$message.success("");
              this.getallmedicine();
              this.listLoading = false;
            })
            .catch(e => {
              console.log(e);
            });
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    editmedichandle(row) {
      this.editmedicdialog = true;
      this.editform = Object.assign({}, row);
    },
    //
    editmedic() {
      this.$confirm(" ", "")
        .then(() => {
          this.$putRequest("/updatemedicine", this.editform)
            .then(res => {
              if (res.data.code == 200) {
                this.$message.success("");
                this.editmedicdialog = false;
              }
              this.getallmedicine();
            })
            .catch(e => {
              console.log(e);
            });
        })
        .catch(() => {
          console.log("cancle");
        });
    },
    //
    addmedic() {
      this.$postRequest("/addmedicine", this.addform)
        .then(res => {
          if (res.data.code == 200) {
            this.$message.success("");
            this.addmedicdialog = false;

            this.getallmedicine();
            // this.$refs.addform.resetFields();
          }
        })
        .catch(e => {});
    },
    //
    selectmedic() {
      this.getallmedicine();
    },
    resetselect() {
      this.medicname = "";
      this.getallmedicine();
    }
  }
};
</script>

