<template>
  <div>
    <!-- -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="menua">
		
		<div >
			<!-- -->
			<template>
			  <el-tabs v-model="activeName" @tab-click="handleClick">
			    <el-tab-pane label="" name="first">
			      <div class="formcard">
			        <el-form ref="userinfoform" :model="userinfoform" label-width="80px">
			          <div>
			            <el-form-item label="">
			              <el-avatar shape="square" :size="60" :src="avatarUrl"></el-avatar>
			              <el-upload
			                action
			                :auto-upload="true"
			                :http-request="handleHttpRequest"
			                :before-upload="beforeAvatarUpload"
			              
			              >
			                <el-button type="primary" round size="mini"></el-button>
			              </el-upload>
			            </el-form-item>
			          </div>
			          <div>
			            <el-form-item label="">
			              <el-input disabled v-model="userinfoform.username"></el-input>
			            </el-form-item>
			          </div>
			          <div>
			            <el-form-item label="">
			              <el-input placeholder="" v-model="userinfoform.realname"></el-input>
			            </el-form-item>
			          </div>
			          <div>
			            <el-form-item label="">
			              <el-radio v-model="userinfoform.sex" label=""></el-radio>
			              <el-radio v-model="userinfoform.sex" label=""></el-radio>
			            </el-form-item>
			          </div>
			          <div>
			            <el-form-item label="">
			              <el-input placeholder="" v-model="userinfoform.email" prop="email"></el-input>
			            </el-form-item>
			          </div>
			          <div>
			            <el-form-item label="">
			              <el-input placeholder="" v-model="userinfoform.phone" prop="phone"></el-input>
			            </el-form-item>
			          </div>
			          <div>
			            <el-form-item label="">
			              <el-input placeholder="" type="textarea" v-model="userinfoform.intro"></el-input>
			            </el-form-item>
			          </div>
			          <div class="upbtn">
			            <el-button @click="updateUserInfo" type="primary"></el-button>
			          </div>
			        </el-form>
			      </div>
			    </el-tab-pane>
			     <!-- -->
			    <el-tab-pane label="" name="second">
			      <div>
			        <div class="securitycard">
			          <div class="formcard">
			            <div>
			              <el-collapse accordion>
			                <el-collapse-item title="" name="1">
			                  <el-form
			                    ref="updateUsername"
			                    :model="securityform"
			                    :rules="securityrules"
			                    label-width="80px"
			                  >
			                    <el-form-item label="" prop="username">
			                      <el-input v-model="securityform.username"></el-input>
			                    </el-form-item>
			                    <el-form-item label="" prop="oldpassword">
			                      <el-input type="password" v-model="securityform.oldpassword"></el-input>
			                    </el-form-item>
			                    <div class="upbtn">
			                      <el-button  @click="upateUsername" type="danger"></el-button>
			                    </div>
			                  </el-form>
			                </el-collapse-item>
			                <el-collapse-item title="" name="2">
			                  <el-form
			                    ref="securityform"
			                    :model="securityform"
			                    :rules="securityrules"
			                    label-width="80px"
			                  >
			                    <el-form-item label="" prop="oldpassword">
			                      <el-input type="password" v-model="securityform.oldpassword"></el-input>
			                    </el-form-item>
			                    <el-form-item label="" prop="newpassword">
			                      <el-input type="password" v-model="securityform.newpassword"></el-input>
			                    </el-form-item>
			                    <el-form-item label="" prop="newpasswordagain">
			                      <el-input type="password" v-model="securityform.newpasswordagain"></el-input>
			                    </el-form-item>
			                    <div class="upbtn">
			                      <el-button @click="updatepassword" type="danger"></el-button>
			                    </div>
			                  </el-form>
			                </el-collapse-item>
			              </el-collapse>
			            </div>
			          </div>
			        </div>
			      </div>
			    </el-tab-pane>
			  </el-tabs>
			</template>
		</div>
      
    </div>
  </div>
</template>

<script>
export default {
  created() {
    this.getUserInfo();
  },
  data() {
    var checkusername = (rule,value,callback)=>{
      if(value===''){
        callback (new Error(''))
    }else {
        this.$getRequest('/checkusername?username='+value)
      .then(res=>{
        if(res.data.code !== 200){
          callback (new Error(''))
        }else{
          callback()
        }
      })
    }
  }
    var checkoldpassword = (rule, value, callback) =>{
      if (value === "") {
        callback (new Error('!'))
      } else{
        this.$getRequest('/checkpassword?password='+value)
        .then(res=>{
          if (res.data.data === false) {
            callback (new Error('!'))
          } else {
            callback()
          }
        })
        .catch(e=>{
          console.log(e)
        })
      }
    };
    var checkpassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value == this.securityform.oldpassword) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var checkpasswordagain = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.securityform.newpassword) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      userid: "",
      avatarUrl: "",
      activeName: "first",
      userinfoform: {
        realname: "",
        username: "",
        sex: "",
        email: "",
        phone: "",
        intro: ""
      },
      securityform: {
        username: "",
        oldpassword: "",
        newpassword: "",
        newpasswordagain: ""
      },
      securityrules: {
        username: [
          { min: 3, max: 10, message: " 3  10 ", trigger: "blur" },
          { validator: checkusername, trigger: "blur" },
        ],
        oldpassword: [
          { validator: checkoldpassword, trigger: "blur" },
          { min: 5, message: "", trigger: "blur" }
        ],
        newpassword: [
          { min: 5, message: "", trigger: "blur" },
          { validator: checkpassword, trigger: "blur" }
        ],
        newpasswordagain: [
          { min: 5, message: "", trigger: "blur" },
          { validator: checkpasswordagain, trigger: "blur" }
        ]
      },
      userinforules:{
        email: [{
            required: true,//
            message: '',//
            trigger: 'blur'//blur
        },
        {
            type: 'email',//numberemaildate
            message: '',
            trigger: ['blur', 'change']
        }],
        phone: [{
        required: true,
        pattern: /^1[34578]\d{9}$/,//
        message: '',
        trigger: 'blur'
    }],
      }
    };
  },
  methods: {
    handleClick(tab, event) {},
    //
    async getUserInfo() {
      await this.$getRequest("/getuserinfo").then(Response => {
        this.userinfoform.realname = Response.data.data.realname;
        this.userinfoform.username = Response.data.data.username;
        this.userinfoform.sex = Response.data.data.sex;
        this.userinfoform.email = Response.data.data.email;
        this.userinfoform.phone = Response.data.data.phone;
        this.userinfoform.intro = Response.data.data.intro;
        this.avatarUrl = this.$baseUrl + Response.data.data.avatar;
        this.userid = Response.data.data.userid;
      });
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error(" JPG /png!");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    handleHttpRequest(img) {
      let imgfile = img.file;
      let formdata = new window.FormData();
      formdata.append("imgfile", imgfile);
      this.$fileRequest("/uploadimage", formdata)
        .then(res => {
          const avatar = this.$baseUrl + res.data.data;
          console.log(avatar + "  ");
          this.avatarUrl = avatar;
          // this.$store.commit('updateAvatar',avatar)
          this.$message.success("!");
        })
        .catch(e => {
          console.log(e);
        });
    },
    updateUserInfo() {
      this.$refs.userinfoform.validate(validate =>{
        if (!validate) {
          return;
        }
        this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          let userinfo = {
            realname: this.userinfoform.realname,
            sex: this.userinfoform.sex,
            email: this.userinfoform.email,
            phone: this.userinfoform.phone,
            intro: this.userinfoform.intro,
            userid: this.userid
          };
          this.$putRequest("/updateUserinfo", userinfo)
            .then(res => {
              if(res.data.code==200)
              this.$message.success("");
            })
            .catch(e => {
              console.log(e);
            });
        })
        .catch(() => {
          this.$message.info("!");
        });
      })
      
    },
    upateUsername() {
      this.$refs.updateUsername.validate(validate => {
        if (!validate) {
          return;
        }
        let user = {
          id: this.userid,
          username:this.securityform.username
        }
        this.$confirm("? ", "", {
          confirmButtonText: "",
          cancelButtonText: ""
        }).then(() => {
          this.$putRequest(
            "/updateuser",
            user
          )
            .then(res => {
              if (res.data.code == 200) {
                this.$message.success("");
                window.sessionStorage.clear();
                this.$router.push("/login");
              }
            })
            .catch(() => {
              this.$message.info("!");
            });
        });
      });
    },
    updatepassword() {
      this.$refs.securityform.validate(validate => {
        if (!validate){
          return false
        }
        let user = {
          id:this.userid,
          password:this.securityform.newpasswordagain
        }
        //
        this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: ""
        })
          .then(res => {
            this.$putRequest(
              "/updateuser",
              user
            )
              .then(res => {
                console.log(res);
                if (res.data.code == 200) {
                  this.$message.success("!");
                  window.sessionStorage.clear();
                  this.$router.push("/login");
                }
              })
              .catch(e => {
                console.log(e);
              });
          })
          .catch(() => {
            this.$message.info("!");
          });
      });
    }
  }
};
</script>
<style>
.breadcrumb {
  margin-top: 20px;
  margin-left: 30px;
}

.formcard {
  padding-top: 10px;
  width: 500px;
}
.upbtn {
  float: right;
}
.avatorimg {
  width: 60px;
}
.uploadimage {
  margin-left: 300px;
}
.userinfocard{
	box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
	border-radius: 4px;
	float: left;
	width: 400px;
	height: 300px;
	
}
</style>
