<template>
	<div>
		<!-- -->
		<div class="breadcrumb">
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path:'/Home' }"></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		
		<div class="menua">
			<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
				<el-form :inline="true">
					<el-form-item>
						<el-input v-model="petname" prefix-icon="el-icon-search" clearable placeholder=""></el-input>
					</el-form-item>
					<el-form-item>
						<el-button @click="selectpet" type="primary"></el-button>
					</el-form-item>
					<el-form-item>
						<el-button @click="resetselect" type="primary"></el-button>
					</el-form-item>
				</el-form>
			</el-col>
			<!---->
			<el-table :data="petdata" v-loading="listLoading" style="width: 100%" border>
				<el-table-column type="index" ></el-table-column>
				<el-table-column width="100px" prop="id" label="ID"sortable></el-table-column>
				<el-table-column prop="name" label="" ></el-table-column>
				<el-table-column prop="sex" label="" ></el-table-column>
				<el-table-column prop="type" label="" ></el-table-column>
				<el-table-column prop="age" label=""></el-table-column>
				<el-table-column prop="user" label="ID" ></el-table-column>
				<el-table-column width="200px" label="">
					<template scope="scope">
						<el-button size="small" @click="editpethandle(scope.row)"></el-button>
						<el-button type="danger" @click="delpet(scope.row)" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!-- -->
			<el-pagination
			  background
			  layout="total, prev, pager, next"
			  :total="total"
			  :page-size="pagesize"
			  :page-count="pagecount"
			  :current-page="currentpage"
			  @current-change="currentpagehandle"
			  style="margin-top:10px"
			></el-pagination>
			<!-- dialog -->
			<el-dialog title="" :visible.sync="editdialog">
			  <div  style="width:300px">
			    <el-form
			      ref="editform"
			      :model="editform"
			      label-width="70px"
			      size="mini"
			    >
			      <el-form-item label="ID">
			        <el-input v-model="editform.id" disabled></el-input>
			      </el-form-item>
			      <el-form-item label="">
			        <el-input v-model="editform.name"></el-input>
			      </el-form-item>
				  <el-form-item label="">
				    <el-radio v-model="editform.sex" label=""></el-radio>
				    <el-radio v-model="editform.sex" label=""></el-radio>
				  </el-form-item>
				  <el-form-item label="">
				    <el-input v-model="editform.type"></el-input>
				  </el-form-item>
			      <el-form-item label="">
			        <el-input-number v-model="editform.age" :precision="1" :step="1" :min="0"></el-input-number>
			      </el-form-item>
				 <el-form-item label="">
				   <el-input v-model="editform.user" ></el-input>
				 </el-form-item>
			      <div>
			        <el-button @click="editdialog = false" size="mini"></el-button>
			        <el-button @click="editpet" type="primary" size="mini"></el-button>
			      </div>
			    </el-form>
			  </div>
			  
			</el-dialog>
		</div>
	</div>
</template>

<script>
	export default {
		created() {
			this.getallpet()
		},
		data() {
			return {
				petdata: [],
				listLoading: false,
				editdialog: false,
				petname: '',
				total: 0, //
				pagesize: 6, //
				pagecount: 0, //
				currentpage: 1, //
				editform: {
				  id: 0,
				  name: "",
				  sex: "",
				  type: "",
				  age: "",
				  image: "",
				  user: 0,
				},
			}
		},
		methods: {
			getallpet() {
				this.$getRequest('/getallpet?' + "pageNo=" +
					this.currentpage +
					"&pageSize=" +
					this.pagesize +
					"&parms=" +
					this.petname)
					.then(res=>{
						if (res.data.code == 200) {
						  this.petdata = res.data.data.list;
						  this.total = res.data.data.total; //
						  this.pagesize = res.data.data.pageSize; //
						  this.pagecount = res.data.data.pages; //
						  this.currentpage = res.data.data.pageNum; //
						}
					})
			},
			selectpet() {
				this.getallpet()
			},
			resetselect() {
				this.petname = ''
				this.getallpet()
			},
			currentpagehandle(){
				

			},
			editpet(){
				this.$confirm(' ', '')
					.then(() => {
						this.$putRequest('/updatepet', this.editform)
							.then(res => {
								if (res.data.code == 200) {
									this.$message.success('');
									this.editmedicdialog = false;
								}
								this.getallpet()();
							})
							.catch(e => {
								console.log(e);
							});
					})
					.catch(() => {
						console.log('cancle');
					});
			},
			editpethandle(row){
				this.editdialog = true;
				this.editform = Object.assign({}, row);
			},
			delpet(row){
				this.$confirm("", "", {
				  type: "warning"
				})
				  .then(() => {
				    this.listLoading = true;
				    this.$deleteRequest("/deltepet?id=" + row.id)
				      .then(res => {
				        this.$message.success("");
				        this.getallpet();
				        this.listLoading = false;
				      })
				      .catch(e => {
				        console.log(e);
				      });
				  })
				  .catch(() => {
				    this.$message.info("");
				  });
			}
		}

	}
</script>

