<template>
  <div>
    <!-- -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!-- -->
    <div class="menua">
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true">
          <el-form-item>
          <el-input prefix-icon="el-icon-search" clearable  placeholder='' v-model="selectname"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button  @click="selectnamehandle" type="primary" ></el-button>
          </el-form-item>
          <el-form-item>
            <el-button  @click="resetselecthandle" type="primary" ></el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="adduserdialog = true" type="primary"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table
        :data="usertabledata"
        v-loading="listLoading"
        style="width: 100%"
        border
      >
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="userid" label="ID" width="100" sortable></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="realname" label="" ></el-table-column>
        <el-table-column prop="sex" label=""></el-table-column>
        <el-table-column prop="phone" label="" ></el-table-column>
        <el-table-column prop="email" label="" ></el-table-column>
        <el-table-column
          :filters="[{text: '',value: 'admin'},{text:'',value: 'user'},{text:'',value:'doctor'}]"
          :filter-method="filterusertype"
          prop="usertype"
          label=""
          width="120"
        >
          <template slot-scope="scope">
            <el-tag>{{scope.row.usertype}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="200">
          <template scope="scope">
            <el-button size="small" @click="edituserhandle(scope.$index, scope.row)"></el-button>
            <el-button type="danger" @click="delteuser(scope.$index,scope.row)" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- -->
      <div>
        <el-pagination
          background
          layout="total, prev, pager, next"
          :total="total"
          :page-size="pagesize"
          :page-count="pagecount"
          :current-page="currentpage"
          @current-change="currentpagehandle"
          style="margin-top:10px"
        ></el-pagination>
      </div>
    </div>
    <!-- dialog-->
    <div>
      <el-dialog title="" :visible.sync="edituserdialog">
        <el-tabs v-model="activeName" type="card">
          <!-- -->
          <el-tab-pane label="" name="first">
            <el-form
              style="width:300px"
              ref="editform"
              :model="editform"
              label-width="70px"
              size="mini"
            >
              <div>
                <el-form-item label="ID">
                  <el-input disabled v-model="editform.userid"></el-input>
                </el-form-item>
              </div>
              <div>
                <el-form-item label="">
                  <el-input disabled v-model="editform.username"></el-input>
                </el-form-item>
              </div>
              <div>
                <el-form-item label="">
                  <el-input v-model="editform.realname"></el-input>
                </el-form-item>
              </div>
              <div>
                <el-form-item label="">
                  <el-radio v-model="editform.sex" label=""></el-radio>
                  <el-radio v-model="editform.sex" label=""></el-radio>
                </el-form-item>
              </div>
              <div>
                <el-form-item label="">
                  <el-input v-model="editform.email"></el-input>
                </el-form-item>
              </div>
              <div>
                <el-form-item label="">
                  <el-input v-model="editform.phone"></el-input>
                </el-form-item>
              </div>
              <el-button @click="edituserdialog = false" size="mini"></el-button>
              <el-button @click="updateuserinfo" type="primary" size="mini"></el-button>
            </el-form>
          </el-tab-pane>
          <!-- -->
          <el-tab-pane label="" name="second">
            <el-form
              style="width:300px"
              ref="editform"
              :model="editform"
              label-width="70px"
              size="mini"
            >
              <div>
                <el-form-item label="ID">
                  <el-input disabled v-model="editform.userid"></el-input>
                </el-form-item>
              </div>
              <div>
                <el-form-item label="">
                  <el-input v-model="editform.username"></el-input>
                </el-form-item>
              </div>
              <div>
                <el-form-item label="">
                  <el-input v-model="editform.password"></el-input>
                </el-form-item>
              </div>
              <el-button @click="edituserdialog = false" size="mini"></el-button>
              <el-button @click="updateuser" type="danger" size="mini"></el-button>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </el-dialog>
      <!-- dialog-->
      <el-dialog title="" :visible.sync="adduserdialog">
        <el-form style="width:300px" ref="addform" :model="addform" label-width="70px" size="mini">
          <div>
            <el-form-item label="">
              <el-input placeholder="" v-model="addform.username"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="">
              <el-input placeholder="" v-model="addform.password" prop="password"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="">
              <el-select style="width:90px" v-model="addform.usertype" placeholder="">
                <el-option
                  v-for="item in usertype"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="">
              <el-input placeholder="" v-model="addform.realname"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="">
              <el-radio v-model="addform.sex" label=""></el-radio>
              <el-radio v-model="addform.sex" label=""></el-radio>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="">
              <el-input placeholder="" v-model="addform.email" prop="email"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="">
              <el-input placeholder="" v-model="addform.phone" prop="phone"></el-input>
            </el-form-item>
          </div>
          <el-button @click="adduserdialog = false" size="mini"></el-button>
          <el-button @click="adduser" type="primary" size="mini"></el-button>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  created() {
    this.getalluser();
  },
  data() {
    return {
      selectname: "",
      total: 0,
      pagesize: 6,
      pagecount: 0,
      currentpage: 1,
      activeName: "first",
      edituserdialog: false,
      adduserdialog: false,
      listLoading: false,
      usertabledata: [],
      usertype: [
        { value: "admin", lable: "" },
        { value: "user", lable: "" },
        { value: "doctor", lable: "" }
      ],
      editform: {
        userid: "",
        realname: "",
        username: "",
        sex: "",
        email: "",
        phone: "",
        password: ""
      },
      addform: {
        username: "",
        usertype: "",
        realname: "",
        sex: "",
        email: "",
        phone: "",
        password: ""
      }
    };
  },
  methods: {
    //
    getalluser() {
      this.listLoading = true;
      let pageParms = {
        pageNo: this.currentpage,
        pageSize: this.pagesize
      };
      console.log(pageParms);
      this.$getRequest("/getalluser?" + "pageNo=" + this.currentpage+"&parms="+this.selectname).then(
        res => {
          if (res.data.code == 200) {
            this.usertabledata = res.data.data.list; //
            this.total = res.data.data.total; //
            this.pagesize = res.data.data.pageSize; //
            this.pagecount = res.data.data.pages; //
            this.currentpage = res.data.data.pageNum; //
            this.listLoading = false;
          } else {
            this.$message.error("");
            this.listLoading = false;
          }
        }
      );
    },
    //
    delteuser(index, row) {
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          this.$deleteRequest("/delteuser?id=" + row.userid)
            .then(res => {
              this.$message.success("");
              this.getalluser();
            })
            .catch(e => {
              console.log(e);
            });
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    filterusertype(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    //dialog
    edituserhandle(index, row) {
      this.edituserdialog = true;
      this.editform = Object.assign({}, row);
    },
    //
    updateuserinfo() {
      this.$confirm(" ", "")
        .then(() => {
         
          this.$putRequest("/updateUserinfo", this.editform)
            .then(res => {
              if (res.data.code == 200) {
                this.$message.success("");
                this.edituserdialog = false;
              }
              this.getalluser();
            })
            .catch(e => {
              console.log(e);
            });
        })
        .catch(() => {
          console.log("cancle");
        });
    },
    //
    updateuser() {
      this.$confirm("", "")
        .then(() => {
          let user = {
            id: this.editform.userid,
            username: this.editform.username,
            password: this.editform.password
          };
          this.$putRequest("/updateuser", user)
            .then(res => {
              if (res.data.code == 200) {
                this.$message.success("");
                this.getalluser()
              }
            })
            .catch(e => {});
        })
        .catch(() => {
          console.log("can");
        });
    },
    //
    adduser() {
      this.$postRequest("/adduser", this.addform)
        .then(res => {
          if (res.data.code == 200) {
            this.$message.success("");
            this.adduserdialog = false;
            this.getalluser();
            // this.$refs.addform.resetFields();
          }
        })
        .catch(e => {});
    },
    //
    currentpagehandle(val) {
      this.currentpage = val;
      this.getalluser();
    },
    //parms
    selectnamehandle() {
      this.listLoading = true;
      this.getalluser()
      this.listLoading = false;
    },
    //
    resetselecthandle(){
       this.listLoading = true;
       this.selectname = ''
       this.getalluser()
       this.listLoading = false;
    }
  }
};
</script>

