<template>
  <div>
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="menua">
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true">
          <el-form-item>
            <el-input v-model="title" prefix-icon="el-icon-search" clearable placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="findmessage" type="primary"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="resetmessage" type="primary"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="addmessagehandle" type="primary"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-table :data="messagedata" style="width: 100%" border>
        <el-table-column width="80" prop="id" label="ID"></el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="createTime" label=""></el-table-column>
        <el-table-column prop="createUser" label=""></el-table-column>
        <el-table-column width="200" label="">
          <template scope="scope">
            <el-button size="small" @click="seemessage(scope.row)"></el-button>
            <el-button type="danger" @click="deltemessage(scope.row)" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- -->
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="total"
        :page-size="pagesize"
        :page-count="pagecount"
        :current-page="currentpage"
        @current-change="currentpagehandle"
        style="margin-top:10px"
      ></el-pagination>
      <!-- dialog -->
      <el-dialog title="" :visible.sync="addmessagedialog">
        <div>
          <el-form ref="editform" :model="editform" label-width="70px" size="mini">
            <el-form-item label="">
              <el-input v-model="editform.title"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editform.createTime"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input :rows="10" type="textarea" v-model="editform.content"></el-input>
            </el-form-item>
            <el-button @click="addmessagedialog = false" size="mini"></el-button>
            <el-button @click="addmessage" type="primary" size="mini"></el-button>
          </el-form>
        </div>
      </el-dialog>
      <!-- dialog -->
      <el-dialog title="" :visible.sync="messagedialog">
        <div>
          <el-form ref="message" :model="message" label-width="70px" size="mini">
            <el-form-item label="">
              <el-input v-model="message.title"></el-input>
            </el-form-item>
            <el-form-item label="ID">
              <el-input v-model="message.createUser"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="message.createTime"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input :rows="10" type="textarea" v-model="message.content"></el-input>
            </el-form-item>
            <div style="margin-left:300px"> 
              <el-button @click="seemessageok" type="success" icon="el-icon-check" circle></el-button>
            </div>
          </el-form>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  created() {
    this.getmessage();
  },
  data() {
    return {
      total: 0, //
      pagesize: 6, //
      pagecount: 0, //
      currentpage: 1, //
      messagedata: [],
      addmessagedialog: false,
      messagedialog: false,
      title: "",
      editform: {
        id: 0,
        title: "",
        content: "",
        createTime: "",
        createUser: 0
      },
      message: {
        id: 0,
        title: "",
        content: "",
        createTime: "",
        createUser: 0
      }
    };
  },
  methods: {
    getmessage() {
      this.$getRequest(
        "/getallmessage?" +
          "pageNo=" +
          this.currentpage +
          "&pageSize=" +
          this.pagesize +
          "&parms=" +
          this.title
      ).then(res => {
        console.log(res);
        this.messagedata = res.data.data.list;
        this.total = res.data.data.total; //
        this.pagesize = res.data.data.pageSize; //
        this.pagecount = res.data.data.pages; //
        this.currentpage = res.data.data.pageNum; //
      });
    },
    currentpagehandle(val) {},
    addmessagehandle() {
      this.gettime();
      this.addmessagedialog = true;
    },
    gettime() {
      var date = new Date();
      var year = date.getFullYear(); //
      var month = date.getMonth() + 1; //
      var day = date.getDate(); //
      var hours = date.getHours(); //
      var minutes = date.getMinutes(); //
      var strDate =
        year + "-" + month + "-" + day + " " + hours + ":" + minutes;
      this.editform.createTime = strDate;
    },
    addmessage() {
      this.$postRequest("/addmessage", this.editform).then(res => {
        if (res.data.code == 200) {
          this.$message.success("");
          this.addmessagedialog = false;
          this.getmessage();
        }
      });
    },
    findmessage() {
      this.getmessage();
    },
    resetmessage() {
      this.title = "";
      this.getmessage();
    },
    deltemessage(row) {
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => {
          this.$deleteRequest("/deltemessage?id=" + row.id)
            .then(res => {
              this.$message.success("");
              this.getmessage();
            })
            .catch(e => {
              console.log(e);
            });
        })
        .catch(() => {
          this.$message.info("");
        });
    },
    seemessage(row) {
      this.messagedialog = true;
      this.message = Object.assign({}, row);
    },
    seemessageok() {
      this.messagedialog = false;
    }
  }
};
</script>

